import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import getLanguage from "../../../App/MyLanguange";
import { Form, Input, Col, Checkbox, Modal } from "antd";

const InputGroup = Input.Group;

class ModalAddUnit extends Component {
  state = {
    check_code: false
  };

  componentDidUpdate = () => {
    const { resetFields } = this.props.form;
    const { success } = this.props.unit;
    if (success) {
      resetFields();
      this.setState({
        check_code: false
      });
    }
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["unit_code"]: null
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { unit } = this.props;
    const { loading } = unit;
    const error = unit.errorField ? unit.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="units.button.add" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <InputGroup compact>
                <Col span={20}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="units.add.code" />}
                    validateStatus={
                      error.unit_code || getFieldError("unit_code")
                        ? "error"
                        : null
                    }
                    help={
                      error.unit_code
                        ? error.unit_code
                        : getFieldError("unit_code")
                    }
                  >
                    {getFieldDecorator("unit_code", {
                      rules: [
                        {
                          max: 30,
                          message: <IntlMessages id="maxLength30" />
                        }
                      ]
                    })(
                      <Input disabled={this.state.check_code ? true : false} />
                    )}
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="autoGenerateCode" />}
                  >
                    <Checkbox
                      style={{
                        paddingTop: "5px",
                        paddingLeft: "8px"
                      }}
                      checked={this.state.check_code}
                      onChange={this.handleChangeCode}
                    />
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="locations.add.name" />}
              >
                {getFieldDecorator("unit_name", {
                  //initialValue: data.description,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>
            </div>
          </div>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddUnit = Form.create({
  name: "modal_add_unit"
})(ModalAddUnit);

export default WrappedModalAddUnit;
