const actions = {
  getItems: (page, search, filter) => ({
    type: 'GET_ITEMS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createItem: () => ({
    type: 'CREATE_ITEM_REQUEST'
  }),
  importRaptorItems: () => ({
    type: "IMPORT_RAPTOR_ITEMS_REQUEST",
  }),
  storeItem: data => ({
    type: 'STORE_ITEM_REQUEST',
    payload: { body: data }
  }),
  storeItemLocation: (id_item, data) => ({
    type: 'STORE_ITEM_LOCATION_REQUEST',
    payload: {
      id_item: id_item,
      body: data
    }
  }),
  editItem: id_item => ({
    type: 'EDIT_ITEM_REQUEST',
    payload: { id_item: id_item }
  }),
  editItemLocation: (id_item, id_location) => ({
    type: 'EDIT_ITEM_LOCATION_REQUEST',
    payload: { 
      id_item: id_item ,
      id_location: id_location
    }
  }),
  updateItem: (id_item, data) => ({
    type: 'UPDATE_ITEM_REQUEST',
    payload: {
      id_item: id_item,
      body: data
    }
  }),
  updateItemLocation: (id_item, id_location, data) => ({
    type: 'UPDATE_ITEM_LOCATION_REQUEST',
    payload: {
      id_item: id_item,
      id_location: id_location,
      body: data
    }
  }),
  updateItemSalesPurchase: (id_item, data) => ({
    type: 'UPDATE_ITEM_SALES_PURCHASE_REQUEST',
    payload: {
      id_item: id_item,
      body: data
    }
  }),
  updateItemGroup: (id_item, data) => ({
    type: 'UPDATE_ITEM_GROUP_REQUEST',
    payload: {
      id_item: id_item,
      body: data
    }
  }),
  updateItemAccount: (id_item, data) => ({
    type: 'UPDATE_ITEM_ACCOUNT_REQUEST',
    payload: {
      id_item: id_item,
      body: data
    }
  }),
  
  showItem: id_item => ({
    type: 'SHOW_ITEM_REQUEST',
    payload: { id_item: id_item }
  }),
  deactivateItem: id_item => ({
    type: 'DEACTIVATE_ITEM_REQUEST',
    payload: { id_item }
  }),
  activateItem: id_item => ({
    type: 'ACTIVATE_ITEM_REQUEST',
    payload: { id_item }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
