import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import ItemCreateDescForm from "./createDescForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/item/actions";
import itemCategoryActions from "../../../../redux/item/itemCategory/actions";
import unitActions from "../../../../redux/item/unit/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/items";
const { storeItemCategory } = itemCategoryActions;
const { storeUnit } = unitActions;

class CreateItem extends Component {
  state = {
    visibleAddItemCategory: false,
    visibleAddUnit: false
  };
  // Get All data diawal
  componentDidMount() {
    const { createItem } = this.props;
    createItem();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.item.error) {
      notification("error", this.props.item.error);
      this.props.clearError();
    }

    if (this.props.item.success) {
      this.props.history.replace(
        redirectTo + "/update/" + this.props.item.id_item
      );
    }

    // Item Category
    if (this.props.itemCategory.error) {
      notification("error", this.props.itemCategory.error);
      this.props.clearError();
    }

    if (this.props.itemCategory.success) {
      const { createItem, clearSuccess } = this.props;
      notification("success", this.props.itemCategory.success);
      this.handleModalAddItemCategory();
      clearSuccess();
      createItem();
    }

    // Unit
    if (this.props.unit.error) {
      notification("error", this.props.unit.error);
      this.props.clearError();
    }

    if (this.props.unit.success) {
      const { createItem, clearSuccess } = this.props;
      notification("success", this.props.unit.success);
      this.handleModalAddUnit();
      clearSuccess();
      createItem();
    }
  }

  handleModalAddItemCategory = () => {
    this.setState({
      visibleAddItemCategory: !this.state.visibleAddItemCategory
    });
  };

  handleModalAddUnit = () => {
    this.setState({
      visibleAddUnit: !this.state.visibleAddUnit
    });
  };

  handleSubmit = value => {
    const { storeItem } = this.props;
    storeItem(value);
  };

  handleSubmitItemCategory = value => {
    const { storeItemCategory } = this.props;
    storeItemCategory(value);
  };

  handleSubmitUnit = value => {
    const { storeUnit } = this.props;
    storeUnit(value);
  };

  render() {
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="items.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="items.tab.description" />} key="1">
              <ItemCreateDescForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                item={this.props.item}
                // Item Category
                itemCategory={this.props.itemCategory}
                visibleAddItemCategory={this.state.visibleAddItemCategory}
                handleModalAddItemCategory={this.handleModalAddItemCategory}
                handleSubmitItemCategory={this.handleSubmitItemCategory}
                // Unit
                unit={this.props.unit}
                visibleAddUnit={this.state.visibleAddUnit}
                handleModalAddUnit={this.handleModalAddUnit}
                handleSubmitUnit={this.handleSubmitUnit}
              />
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    item: state.item,
    itemCategory: state.itemCategory,
    unit: state.unit
  };
};

export default compose(
  connect(mapStateToProps, { ...actions, storeItemCategory, storeUnit })
)(CreateItem);
