import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import getLanguage from "../../../App/MyLanguange";
import { Form, Input, Col, Modal, TreeSelect, Row } from "antd";

class ModalAddItemCategory extends Component {
  componentDidUpdate = () => {
    const { resetFields } = this.props.form;
    const { success } = this.props.itemCategory;
    if (success) {
      resetFields();
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { itemCategory } = this.props;
    const loading = itemCategory.loadingSubmit;
    const error = itemCategory.errorField ? itemCategory.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="itemCategories.add.subtitle.data" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row>
            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="itemCategories.parent" />}
              >
                {getFieldDecorator("id_item_category_parent")(
                  <TreeSelect
                    style={{ width: "100%" }}
                    allowClear={true}
                    showSearch={true}
                    treeNodeFilterProp="title"
                    dropdownStyle={{ maxHeight: 300, overflow: "auto" }}
                    treeData={this.props.item_categories}
                    treeDefaultExpandAll
                    loading={this.props.item.loading}
                  />
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="itemCategories.code" />}
                validateStatus={
                  error.item_category_code ||
                  getFieldError("item_category_code")
                    ? "error"
                    : null
                }
                help={
                  error.item_category_code
                    ? error.item_category_code
                    : getFieldError("item_category_code")
                }
              >
                {getFieldDecorator("item_category_code", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 30,
                      message: <IntlMessages id="maxLength30" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="itemCategories.name" />}
              >
                {getFieldDecorator("item_category_name", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddItemCategory = Form.create({
  name: "modal_add_item_category"
})(ModalAddItemCategory);

export default WrappedModalAddItemCategory;
